import MsgUser from './MsgUser'
import UserAvatar from './UserAvatar'
import logo from '@/assets/logo.png'
export default {
  props: ['data', 'index'],
  data () {
    return {
      logo: logo
    }
  },
  components: {
    MsgUser,
    UserAvatar
  },
  methods: {
    avatar () {
      this.$emit('onAvatar')
    },
    share () {
      this.$emit('onShare', this.data)
    },
    deleteMsg () {
      this.$emit('onDelete', this.data)
    }
  }
}



